<template>
    <div class="center">
     <div class="center_breadcrumb">
         <crumb :data="pageTitle"></crumb>
    </div>
    <div class="center_content">
        <div class="center_content_search">
            <div class="center_content_search_left">
                <div class="search-input-suffix gap">
                    <label>所属年级：</label>
                     <el-select v-model="page.ext2" @change="gradesChange" placeholder="请选择">
                        <el-option v-for="item in gradesOptions" :key="item.id" :label="item.name" :value="item.id">
                        </el-option>
                    </el-select>
                </div>
                <div v-if="type === '0'" class="search-input-suffix">
                    <label>所属班级：</label>
                    <el-select v-model="page.classId" placeholder="请选择">
                        <el-option v-for="item in classOptions" :key="item.classId" :label="item.className" :value="item.classId">
                        </el-option>
                    </el-select>
                </div>
               <div class="search--btn" style="margin: 0 0 0 20px" @click="search">搜索</div>
            </div>
        </div>
         <div class="table-container">
             <el-table :data="tableData"  stripe  style="width: 100%">
                    <el-table-column v-if="type === '0'" prop="usercode" label="学生学号" width="200">
                    </el-table-column>
                    <el-table-column v-if="type === '1'" prop="number" label="教师编号" width="200">
                    </el-table-column>
                    <el-table-column prop="username" :label="type === '0' ? '学生姓名' : '教师姓名'">
                    </el-table-column>
                    <el-table-column v-if="type === '1'" prop="classname" label="所带课程">
                    </el-table-column>
                    <el-table-column v-if="type === '0'" prop="specialtyname" label="所属专业">
                    </el-table-column>
                    <el-table-column v-if="type === '0'" prop="grade" label="所属年级">
                    </el-table-column>
                    <el-table-column v-if="type === '0'" prop="classname" label="所属班级">
                    </el-table-column>
                    <el-table-column label="操作" width="120">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click="handleView(scope.row.userid)" v-havaRole="'graduationRequirementsAnalysisView'">
                                <i class="el-icon-view"></i>&nbsp;&nbsp;查看答卷
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            <div class="center_pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    background
                    :current-page="page.page"
                    :page-sizes="[15,30,40,50]"
                    :page-size="page.pageSize"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</div> 
</template>

<script>
export default {
    data() {
        return {
            pageTitle:['调查进度', '原始答卷'],
            type:this.$route.query.type,
            page:{            
                ext2:'', //年级
                classId:'', //班级
                formID:this.$route.query.id,
                page:1,
                pageSize:15
            },
            gradesOptions:[],
            classOptions:[],
            tableData: [{}],
            total: 0,
            bodyLoading: true,
            api:{
                listOriginalPage:'/form/rest/FormAnserBusiness/listOriginalPage/v1',
                grades:'/basic/class/grades', //年级
                classall: grade => `/basic/class/gradeByall/${grade}` //所属班级
            }
        }
    },
    mounted() {
        this.initData()
        this.getGrades()
    },
    methods: {
        initData(){
            this.$post(this.api.listOriginalPage, this.page).then(res =>{
                 this.bodyLoading = false
                if(res.code === 200){
                    this.tableData = res.data.rows || []
                    this.total = res.data.total
                }
                
            })
        },
        getGrades(){
            this.$get(this.api.grades).then(res=>{
                if(res.status ===1){
                     this.gradesOptions = res.data || []
                     this.gradesOptions.unshift({id:'',name:'全部 '})
                }
               
            })
        },
        search(){
          this.page.page=1
          this.page.pageSize=15
          this.initData()
        },
        gradesChange(){
            this.classall(this.page.ext2)
        },
        classall(grade){
            this.$get(this.api.classall(grade)).then(res=>{
                if(res.status ===1){
                     this.classOptions = res.data || []
                     this.classOptions.unshift({classId:'',className:'全部 '})
                }
               
            })
        },
        handleView(userid) {
         this.$router.push({
            path:'/assessmentOfGraduationRequirements/aResult',
            query:{
                formId:this.page.formID,
                userId: userid
            }
         })
        },
        handleSizeChange(val) {
            this.page.pageSize = val;
            this.initData()
        },
        handleCurrentChange(val) {
            this.page.page = val;
            this.initData()
        }
    }
}
</script>

<style lang="scss">
 @import '../../styles/center.scss';

</style>



